    // 获取 日周奖池+input 那一块
    async getAll1 () {
      let address = await this.$contract.defaultAddress()
      address = address.base58
      let res = await this.$contract.dayWeekData1(address)
      // 日奖池ID, 倒计时, usdt数量
      this.dayPoolId = this.$util.hexToDec(res._dayPoolId._hex)
      let date1 = this.$util.hexToDec(res._dayPoolDate._hex)
      date1 += 86400
      let nowTime1 = Math.floor(Date.now() / 1000)
      // 获取时间后计算出来的倒计时
      let downTime1 = date1 - nowTime1
      clearInterval(this.dayTimer1)
      this.dayTimer1 = setInterval(() => {
        downTime1 -= 1
        this.dayDownTime = this.$util.dayFormat(downTime1)
      }, 1000)
      this.dayUsdtAmount = this.$util.hexTo10To6number3(res._dayPoolAmount._hex)

      // 周奖池ID, 倒计时, usdt数量
      this.weekPoolId = this.$util.hexToDec(res._weekPoolId._hex)
      let date2 = this.$util.hexToDec(res._weekPoolDate._hex)
      date2 += 86400 * 7
      let nowTime2 = Math.floor(Date.now() / 1000)
      // 获取时间后计算出来的倒计时
      let downTime2 = date2 - nowTime2
      clearInterval(this.dayTimer2)
      this.dayTimer2 = setInterval(() => {
        downTime2 -= 1
        this.weekDownTime = this.$util.weekFormat(downTime2)
      }, 1000)
      this.weekUsdtAmount = this.$util.hexTo10To6number3(
        res._weekPoolAmount._hex
      )

      /* 新加 - 修改*/
      let res2 = await this.$contract.dayPoolValue(this.dayPoolId, address)
      let res3 = await this.$contract.weekPoolValue(this.dayPoolId, address)
      // 获取日奖池第四名
      let res4 = await this.$contract.dayPoolRank(this.dayPoolId, 3)
      this.v3.dayChampion4 = this.$util.addressCurtail(
        await this.$contract.addressFromHex(res4)
      )
      console.log('res2:', res2, res3, res4)
      // 今日邀请金额(数据不确定, 暂时用着)
      this.v2.performance = this.$util.hexTo10To6number3(res2)
      // 本轮邀请业绩(数据不确定, 暂时用着)
      this.v2.wholeLine = this.$util.hexTo10To6number3(res3)
      console.log('All1返回值:', res)
    },

