    // 用户授权usdt给主合约
    async approveUsdt () {
      if (this.usdtLoading) {
        console.log('正在加载, 不能重复点击')
        return
      }
      this.usdtLoading = true
      // 判断波长币够不够执行手续费
      let address = await this.$contract.defaultAddress()
      address = address.base58
      let balance1 = await this.$contract.getTRXBalance(address)
      balance1 = this.$util.div10e6(balance1)
      if (balance1 < this.$contract.gTronTips) {
        alert(this.$t('Tips11'))
      }
      // 发生授权usdt交易
      let res
      try {
        res = await this.$contract.approveMaster()
      } catch (e) {
        this.usdtLoading = false
      }
      // 重新刷新
      await this.isApproveUsdt()
      console.log('授权usdt返回值', res)
    },
